import Vue from 'vue'
import Router from 'vue-router'
import constans from '@/assets/js/constans.js'
import header from '@/components/HEAD.vue' // 首页
import compontCurmbs from '@/components/CURMBS.vue'
import footer from '@/components/FOOTER.vue'
import login from '../views/login.vue' // 登录
import wrap from '@/components/WRAP.vue'

// import signUp from '../views/signUp.vue' // 注册
import gwList from '@/views/personCenter/gwList.vue'
import nodeList from '@/views/personCenter/nodeList.vue'
import supplyMan from '@/views/personCenter/supplyMan.vue' // 用户管理
// import groupMan from '@/views/personCenter/groupMan.vue' // 分类管理
// import apiMan from '@/views/personCenter/apiMan.vue' // api管理
import openApplyMan from '@/views/personCenter/openApplyMan.vue' // 开放应用管理
import openApplyDetail from '@/views/personCenter/openAppDetail.vue' // 开放应用详情
import apiDetail from '@/views/personCenter/apiDetail.vue' // api详情
import guideDetail from '@/views/personCenter/guideDetail.vue' // 指南详情
import diyApplyMan from '@/views/personCenter/diyApplyMan.vue' // 定制应用管理
import diyApplyDetail from '@/views/personCenter/diyAppDetail.vue' // 定制应用详情

Vue.use(Router)

let router = new Router({
  // base: '../',
  // mode: 'history',
  routes: [
    /* {
      path: '*',
      redirect: {
        name: 'gwList'
      }
    }, */
    {
      path: '/',
      redirect: {
        name: 'login'
      }
    }, {// 登录
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: '登录'
      }
    },
    { // 人员管理
      path: '/openCenter',
      redirect: {
        name: 'supplyMan'
      },
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: wrap,
        footer: footer
      },
      children: [
        { // 开放中心-提供者管理
          path: 'supplyMan',
          name: 'supplyMan',
          component: supplyMan,
          meta: {
            title: '提供者管理',
            userType: constans['SUPPLY_USER'],
            index: 0,
            curmbsIndex: 1, // 当前点击面包屑层级
            navLeft: config.navLeft,
            needToLogin: true
          }
        },
        { // 开放中心-开发者管理
          path: 'developerMan',
          name: 'developerMan',
          component: supplyMan,
          meta: {
            title: '开发者管理',
            userType: constans['SERVICE_USER'],
            index: 1,
            curmbsIndex: 1, // 当前点击面包屑层级
            navLeft: config.navLeft,
            needToLogin: true
          }
        }]
    },
    { // 开放中心-开放应用管理
      path: '/openApply',
      redirect: {
        name: 'openApplyMan'
      },
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: wrap,
        footer: footer
      },
      children: [{
        path: 'openApplyList',
        name: 'openApplyMan',
        component: openApplyMan,
        meta: {
          title: '开放应用管理',
          opera: true,
          curmbsIndex: 1, // 当前点击面包屑层级
          pName: 'open',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // 开放中心-开放应用详情
        path: 'openApplyDetail',
        name: 'openAppDetail',
        component: openApplyDetail,
        meta: {
          title: 'API列表',
          curmbsIndex: 2, // 当前点击面包屑层级
          pName: 'open',
          opera: true,
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // 开放中心-api详情
        path: 'apiDetail',
        name: 'openApiDetail',
        component: apiDetail,
        meta: {
          title: 'API详情',
          opera: true,
          curmbsIndex: 3, // 当前点击面包屑层级
          pName: 'open',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // 开放中心-指南详情
        path: 'guideDetail',
        name: 'openGuideDetail',
        component: guideDetail,
        meta: {
          title: '指南详情',
          pName: 'open',
          navLeft: config.navLeft,
          needToLogin: true,
          curmbsIndex: 3 // 当前点击面包屑层级
        }
      }]
    },
    { // 开放中心-定制应用
      path: '/diyApply',
      redirect: {
        name: 'diyApplyMan'
      },
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: wrap,
        footer: footer
      },
      children: [{
        path: 'diyApplyMan',
        name: 'diyApplyMan',
        component: diyApplyMan,
        meta: {
          title: '定制应用管理',
          curmbsIndex: 1, // 当前点击面包屑层级
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // 开放中心-定制应用详情
        path: 'diyApplyDetail',
        name: 'diyApplyDetail',
        component: diyApplyDetail,
        meta: {
          title: '定制应用详情',
          curmbsIndex: 2, // 当前点击面包屑层级
          pName: 'diy',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, {
        path: 'openApplyDetail',
        name: 'diyAppDetail',
        component: openApplyDetail,
        meta: {
          title: 'API列表',
          curmbsIndex: 3, // 当前点击面包屑层级
          pName: 'diy',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // 开放中心-api详情
        path: 'apiDetail',
        name: 'diyApiDetail',
        component: apiDetail,
        meta: {
          title: 'API详情',
          curmbsIndex: 4, // 当前点击面包屑层级
          pName: 'diy',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // 开放中心-指南详情
        path: 'guideDetail',
        name: 'diyGuideDetail',
        component: guideDetail,
        meta: {
          title: '指南详情',
          curmbsIndex: 4, // 当前点击面包屑层级
          pName: 'diy',
          navLeft: config.navLeft,
          needToLogin: true
        }
      }]
    },
    { // 开放中心-网关列表
      path: '/gwMan',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: wrap,
        footer: footer
      },
      redirect: {
        name: 'gwList'
      },
      children: [{
        path: 'gwList',
        name: 'gwList',
        component: gwList,
        meta: {
          title: '网关列表',
          curmbsIndex: 1, // 当前点击面包屑层级
          navLeft: config.navLeft,
          needToLogin: true
        }
      }, { // 开放中心-节点列表
        path: 'nodeList',
        name: 'nodeList',
        component: nodeList,
        meta: {
          title: '节点列表',
          curmbsIndex: 2, // 当前点击面包屑层级
          navLeft: config.navLeft,
          needToLogin: true
        }
      }]
    }]
})

import * as types from '@/store/mutation-types.js'
import config from '@/assets/js/config.js'
// 权限拦截
router.beforeEach((to, from, next) => {
  router.app.curmbsData = to.meta.curmbsData
  let userInfo = localStorage.getItem('adminSession')
  let oUserInfo
  if (userInfo) {
    userInfo = JSON.parse(userInfo)
    let __time = userInfo.time * 1
    oUserInfo = userInfo.userInfo
    let sNowTime = new Date().getTime() * 1
    let expiredTime = config.expiredTime // 过期时间
    oUserInfo = sNowTime - __time < expiredTime ? oUserInfo : null
    router.app.$options.store.commit(types.UPDATEUSERINFO, oUserInfo)
  }
  if (!router.app.userInfo && !userInfo && !oUserInfo && to.meta.needToLogin) { // 没有用户数据且页面有权限则跳到首页
    return next({name: 'index'})
  }
  next()
})
export default router



