<template>
  <section class="open-wrap-content">
    <!--<router-link :to="{name: 'addOpenApply'}" class="open-group-add" v-if="$route.meta.opera">添加开放应用</router-link>-->
    <el-tabs type="border-card" v-if="$route.meta.opera">
      <el-tab-pane label="未审核">
        <compontOpenApply type ="applyList" :checkState="constans['STATE_UNAUDITED']"></compontOpenApply>
      </el-tab-pane>
      <el-tab-pane label="审核通过">
        <findPagedApps type ="findPagedApps"></findPagedApps>
      </el-tab-pane>
      <el-tab-pane label="审核驳回">
        <compontOpenApply type ="applyList" :checkState="constans['STATE_NOPASS']"></compontOpenApply>
      </el-tab-pane>
      <el-tab-pane label="未提交">
        <compontOpenApply type ="applyList" :checkState="constans['STATE_UNCOMMITTED']"></compontOpenApply>
      </el-tab-pane>
    </el-tabs>
    <findPagedApps type ="findPagedApps" v-else></findPagedApps>
  </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import compontOpenApply from '@/views/personCenter/openApply.vue'
  import findPagedApps from '@/views/personCenter/findPagedApps.vue'
  import compontSearch from '@/components/SEARCH.vue'
  export default{
    name: 'app-list',
    data () {
      return {
        constans: constans // 常量
      }
    },
    created () {
    },
    methods: {
    },
    components: { compontOpenApply, findPagedApps, compontSearch }
  }
</script>



