<template>
  <section class="open-wrap-content">
    <!--<router-link :to="{name: 'createApp'}" class="open-group-add">添加应用</router-link>-->
    <compontSearch @searchData="fetch(1)" :ruleForm="searchRuleForm" :ruleData="searchRuleData"></compontSearch>
    <compontTable :tableData ="applyList" @handleCurrentChange="handleCurrentChange"></compontTable>
    <el-dialog title="批量审核" :visible.sync="visible">
      <compontCheck @batchReview="submitCheck"></compontCheck>
    </el-dialog>
  </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontTable from '@/components/TABLE.vue'
  import userService from '@/api/userService.js'
  import compontCheck from '@/components/CHECK.vue'
  import compontSearch from '@/components/SEARCH.vue'
  export default{
    name: 'Apply-list',
    data () {
      return {
        constans: constans, // 常量
        applyList: {
          head: [{
            select: true,
            state:[constans['STATE_UNAUDITED']]
          }, {
            label: '应用名称', prop: 'applyName', route: { name: 'diyApplyDetail', query: { applyId: 'id', productId: 'productAuthCode', name: 'applyName'}}
          }, {
            label: '产品名称', prop: 'productName'
          }, {
            label: '状态', prop: 'checkState'
          }, /* {
            label: '创建时间' , prop: 'createDate'
          }, */ {
            label: '操作',
            data: [{
              name: '审核',
              route: { name: 'diyApplyDetail', query: { applyId: 'id', productId: 'productAuthCode', check: 'checkState'}},
              state:[constans['STATE_UNAUDITED']]
            }]
          }],
          data: [],
          pageData: {
            total: 0,
            pageSize: 10,
            currentPage: 1
          },
          btn: [{
            'name': '批量审核', 'callBack': this.showPop
          }
          ]
        },
        visible: false,
        searchRuleForm: {
          applyName: '',
          productName: '',
          checkState: ''
        }, // 搜索
        searchRuleData: {
          applyName: {
            name: '定制应用名称:',
            type: 'input',
            placeholder: '请输入定制应用名称'
          },
          productName: {
            name: '产品名称',
            type: 'input',
            placeholder: '请输入产品名称'
          },
          checkState: {
            name: '状态:',
            type: 'select',
            data: {'': '全部', ...config['checkState']},
            placeholder: '请选择状态'
          }
        }, // 搜索列表
      }
    },
    computed: {
    },
    created () {
      this.fetch()
    },
    methods: {
      //获取列表
      fetch (currentPage = this.applyList.pageData.currentPage) {
        this.applyList.data = []
        let pageSize = this.applyList.pageData.pageSize
        this.applyList.pageData.total = 0
        this.applyList.data = []
        userService.getResponseByBody('diyApplyList', {currentPage, pageSize}, this.searchRuleForm)
          .then((response) => {
            this.applyList.data = response.items
            this.applyList.pageData.total = response.totalNumber
            this.applyList.pageData.currentPage = currentPage
          })
          .catch( error => {
            this.$message.error(error)
          })
      },
      handleCurrentChange (pIndex) {
        this.applyList.pageData.currentPage = pIndex
        this.fetch()
      },
      // 批量审核
      submitCheck (ruleForm) {
        ruleForm.ids = this.id
        this.visible = false
        userService.getResponse('batchDiyApply', ruleForm)
          .then(response => {
            this.fetch()
          })
          .catch(error => {
            console.log(error)
          })
      },
      //打开弹窗
      showPop (data) {
        this.visible = true
        let id = []
        if (data.constructor === Array) {
          data.map(item=>{
            id.push(item.id)
          })
          this.id = id.join(',')
        } else {
          this.id = data.id
        }
      }
    },
    components: { compontTable, compontCheck, compontSearch }
  }
</script>



