<template>
  <section class="open-wrap-content">
    <el-tabs type="border-card">
      <el-tab-pane label="定制应用信息">
        <el-form :model="ruleForm" ref="ruleForm" label-position="left">
          <el-form-item label="appKey">
            <el-input v-model.trim="ruleForm.clientId" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="appSecret">
            <el-input v-model.trim="ruleForm.secret" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="应用名称">
            <el-input v-model.trim="ruleForm.applyName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="已购产品名称">
            <el-input v-model.trim="ruleForm.productName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="访问域名">
            <el-input v-model.trim="ruleForm.domainUrl" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="访问频次">
            <el-radio class="radio" v-model="ruleForm.frequencyType" :label="key*1" v-for="(value, key) in frequencyType" :disabled="true">{{value}}</el-radio>
            <!--<el-radio class="radio" v-model="ruleForm.frequencyType" label="2">定制</el-radio>
            <el-radio class="radio" v-model="ruleForm.frequencyType" label="3">高定制</el-radio>-->
            <!-- <el-radio class="radio" v-model="ruleForm.frequencyType" label="4">手动设置</el-radio> -->
          </el-form-item>
          <el-form-item label="应用描述">
            <el-input type="textarea" :rows="5" v-model.trim="ruleForm.applyDesc" :disabled="true"></el-input>
          </el-form-item>
        </el-form>
        <compontCheck @batchReview="submitCheck" v-if="$route.query.check"></compontCheck>
      </el-tab-pane>
      <el-tab-pane label="开放应用列表">
        <findPagedApps type ="findPagedApps"></findPagedApps>
      </el-tab-pane>
    </el-tabs>
  </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import config from '@/assets/js/config.js'
  import userService from '@/api/userService.js'
  import findPagedApps from '@/views/personCenter/findPagedApps.vue'
  import compontCheck from '@/components/CHECK.vue'
  // import * as types from '@/store/mutation-types'
  export default{
    name: 'apply-detail',
    data () {
      return {
        applyId: this.$route.query.applyId,
        ruleForm: {},
        frequencyType: {
          '1': '标准',
          '2': '定制',
          '3': '高定制'
        }
      }
    },
    created () {
      // this.applyId && this.$store.commit(types.CLICKDIYAPPLY, this.applyId)
      this.getApplyDetail()
    },
    methods: {
      // 获取应用详情
      getApplyDetail() {
        let applyId = this.applyId
        userService.getResponse('diyApplyDetail', {applyId: applyId})
          .then((response) => {
            this.ruleForm = response
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      // 批量审核
      submitCheck (ruleForm) {
        ruleForm.ids = this.applyId
        this.visible = false
        userService.getResponse('batchDiyApply', ruleForm)
          .then(response => {
            this.$router.push({
              name: 'diyApplyMan'
            })
          })
          .catch(error => {
            console.log(error)
          })
      },
    },
    components: { findPagedApps, compontCheck }
  }
</script>



