<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
    <el-form-item label="审核结果" class="open-form-input" prop="checkState">
      <el-radio-group v-model="ruleForm.checkState">
        <el-radio v-for="(state, key) in checkState" class="radio" :label="key" v-if="key==constans['STATE_PASS'] || key==constans['STATE_NOPASS']">{{state}}</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="备注" prop="checkMem" class="open-form-input" v-if="ruleForm.checkState == constans['STATE_NOPASS']">
      <el-input v-model="ruleForm.checkMem" placeholder="请输入备注"></el-input>
    </el-form-item>
    <el-form-item class="open-center">
      <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')">提交</el-button>
      <!--<el-button class="open-login-btn" type="primary" @click="applyForm('ruleForm')">发布</el-button>-->
    </el-form-item>
  </el-form>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  export default{
    name: 'check',
    data () {
      return {
        ruleForm: {
          checkState: '2',
          checkMem: ''
        },
        rules: {
          checkState: [{
            required: true,
            trigger: 'blur',
            message: '请选择审核状态'
          }],
          checkMem: [{
            required: true,
            trigger: 'blur',
            message: '请填写审核失败的原因'
          }]
        },
        checkState: {},
        constans: constans
      }
    },
    created () {
      this.checkState[constans['STATE_UNCOMMITTED']] = '未提交'
      this.checkState[constans['STATE_UNAUDITED']] = '未审核'
      this.checkState[constans['STATE_PASS']] = '审核通过'
      this.checkState[constans['STATE_NOPASS']] = '审核未通过' // 审核状态
    },
    methods: {
      submitForm (ruleForm) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            this.$emit('batchReview', this.ruleForm)
            this.ruleForm = {
              checkState: '2',
              checkMem: ''
            }
          } else {
            return false
          }
        })
      },
    },
    components: {}
  }
</script>



