import {config, errorCode, errorCodeByType, constans} from './config.js'
import ajax from './api.js'
/*
 所有用户的api
 */
class UserService {
  async getResponse (type, data) {
    let apiType = config[type] || {url: '', method: 'POST'}
    try {
      let response = await ajax(apiType.url, data, apiType.method, apiType.baseUrl)
      return this.resolve(response, type)
    } catch (e) {
      console.log('UserService', e)
      await Promise.reject(e) // 所有的错误从这返回调用者的catch
    }
  }
  async getResponseByBody (type, urlData, data) {
    let apiType = config[type]
    let url = apiType.url
    for (var key in urlData) {
      url.indexOf('?') > 0 ? url += `&${key}=${urlData[key]}` : url += `?${key}=${urlData[key]}`
    }
    try {
      let response = await ajax(url, data, apiType.method)
      return this.resolve(response, type)
    } catch (e) {
      console.log('UserService', e)
      await Promise.reject(e) // 所有的错误从这返回调用者的catch
    }
  }
  resolve (response, type) {
    let oData = response.data
    let sErrorCode = oData.errorCode
    let oResponseData = oData.data
    let errMsg = ((errorCodeByType[type] && errorCodeByType[type][sErrorCode]) || errorCode[sErrorCode] || errorCode[constans['DEFAULT_CODE']])['message']
    console.log('+++++++++', sErrorCode === constans['RIGHT_CODE'])
    return sErrorCode === constans['RIGHT_CODE'] ? oResponseData : Promise.reject(errMsg)
  }
  login () {
    /* this.$message.error(message)
     this.$router.push({
     name: 'login'
     })
     this.$store.commit(types.UPDATEUSERINFO, null) */
  }
}
export default new UserService()



