<template>
    <section>
      <compontSearch @searchData="fetch(searchRuleForm['applyName'], 1)" :ruleForm="searchRuleForm" :ruleData="searchRuleData"></compontSearch>
      <compontTable :tableData ="applyList" @handleCurrentChange="handleCurrentChange"></compontTable>
      <el-dialog title="批量审核" :visible.sync="visible">
        <compontCheck @batchReview="submitCheck"></compontCheck>
      </el-dialog>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import userService from '@/api/userService'
  import compontTable from '@/components/TABLE.vue'
  import compontCheck from '@/components/CHECK.vue'
  import compontSearch from '@/components/SEARCH.vue'
  export default{
    name: 'group-list',
    data () {
      return {
        constans: constans, // 常量
        productId: this.$route.query.productId,
        applyList: {
          head: [{
            label: '应用名称', prop: 'applyName'
          }, {
            label: '应用key', prop: 'applyKey'
          }, {
            label: '描述', prop: 'applyDesc'
          }],
          data: [],
          pageData: {
            total: 0,
            pageSize: 10,
            currentPage: 1
          }
        },
        visible: false,
        searchRuleForm: {
          applyName: ''
        }, // 搜索
        searchRuleData: {
          applyName: {
            name: '名称:',
            type: 'input',
            placeholder: '请输入开放应用名称'
          }
        } // 搜索列表
      }
    },
    props: ['type', 'checkState'],
    created () {
      if (this.$route.meta.opera && this.checkState == constans['STATE_UNAUDITED']) { // 可以操作
        this.applyList.head.unshift({
          select: true,
          state: [constans['STATE_UNAUDITED']]
        })
        this.applyList.head.push({
          label: '操作',
          data: [{
            name: '审核', 'callBack': this.showPop
          }]
        })
        this.applyList.btn = [{ // 批量操作
          'name': '批量审核', 'callBack': this.showPop
        }]
      }
      if(this.checkState==constans['STATE_NOPASS']){
        this.applyList.head.splice(3, 0, {
          label: '失败原因', prop: 'checkMem',
        })
      }
      this.fetch()
    },
    methods: {
      //获取列表
      fetch (appName = this.searchRuleForm['applyName'], currentPage = this.applyList.pageData.currentPage) {
        let pageSize = this.applyList.pageData.pageSize
        this.applyList.data = []
        this.applyList.pageData.total = 0
        let checkState = this.checkState
        userService.getResponseByBody(this.type, {currentPage, pageSize}, {checkState, appName})
          .then((response) => {
            let applyList = response.items
            this.applyList.data = applyList
            this.applyList.pageData.total = response.totalNumber
            this.applyList.pageData.currentPage = currentPage
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.applyList.pageData.currentPage = pIndex
        this.fetch(appName)
      },
      // 批量审核
      submitCheck (ruleForm) {
        ruleForm.ids = this.id
        this.visible = false
        userService.getResponse('batchOpenApply', ruleForm)
          .then(response => {
            this.$router.go(0)
          })
          .catch(error => {
            console.log(error)
          })
      },
      //打开弹窗
      showPop (data) {
        this.visible = true
        let id = []
        if (data.constructor === Array) {
          data.map(item=>{
            id.push(item.id)
          })
          this.id = id.join(',')
        } else {
          this.id = data.id
        }
      }
    },
    components: { compontTable, compontCheck, compontSearch }
  }
</script>



