import * as types from '../mutation-types'
// import config from '@/assets/js/config.js'
// initial state
// shape: [{ id, quantity }]
const state = {
  // domain:'', // 保存后台请求的地址，修改时方便（比方说从测试服改成正式服域名）
  curmbsData: localStorage.getItem('adminCurmbsData') && typeof localStorage.getItem('adminCurmbsData') === 'string' ? JSON.parse(localStorage.getItem('adminCurmbsData')) : []
}

// getters
const getters = {

}

// actions
const actions = {
}

// mutations
const mutations = {
  [types.ADDCURMBSDATA] (state, obj) {
    state.curmbsData.push(obj)
    state.curmbsData.length && localStorage.setItem('adminCurmbsData', JSON.stringify(state.curmbsData))
  },
  [types.REMOVECURMBSDATA] (state, num) {
    state.curmbsData.length && state.curmbsData.splice(num - 1, state.curmbsData.length - num + 1)
    localStorage.setItem('adminCurmbsData', JSON.stringify(state.curmbsData))
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



