<template>
  <section>
      <section class="open-wrap-content">
        <!--<compontSearch @searchData="fetch(codId, 1, pageSize, searchRuleForm['nodeName'], searchRuleForm['nodeStatus'])" :ruleForm="searchRuleForm" :ruleData="searchRuleData"></compontSearch>-->
        <!--<a class="open-group-add" @click="showPop">添加节点</a>-->
        <el-table :data="nodeList" border scope="scope">
          <!--<el-table-column label="编号" type="index" width="80"></el-table-column>-->
          <el-table-column label="名称" prop="nodeName"></el-table-column>
          <el-table-column label="ip" prop="nodeUrl"></el-table-column>
          <el-table-column label="状态" prop="nodeStatus" :formatter="formatter"></el-table-column>
          <el-table-column label="描述" prop="nodeDesc" show-overflow-tooltip></el-table-column>
          <!--<el-table-column label="操作">
            <template scope="scope">
              <a class="open-list-btn" @click="delNode(scope.$index, scope.row.nodeId)">删除</a>
              <a class="open-list-btn" size="small" @click="showPop(scope.$index)">编辑</a>
            </template>
          </el-table-column>-->
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="total, prev, pager, next" :total="total" :page-size="pageSize" class="open-page" v-if="total"></el-pagination>
      </section>

      <el-dialog :title="title" :visible.sync="visible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
          <el-form-item label="名称" prop="nodeName" class="open-form-input">
            <el-input v-model="ruleForm.nodeName" placeholder="请输入节点名称"></el-input>
          </el-form-item>
          <el-form-item label="节点ip" prop="nodeUrl" class="open-form-input">
            <el-input v-model="ruleForm.nodeUrl" placeholder="请输入节点ip"></el-input>
          </el-form-item>
          <el-form-item label="节点描述" prop="nodeDesc" class="open-form-input">
            <el-input v-model="ruleForm.nodeDesc" text="textarea" :rows="2" placeholder="请输入节点描述"></el-input>
          </el-form-item>
          <el-form-item class="open-center">
            <el-button class="open-login-btn" @click="visible=false">取消</el-button>
            <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')">保存</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import compontSearch from '@/components/SEARCH.vue'
  import userService from '@/api/userService.js'
  export default{
    name: 'node-list',
    data () {
      return {
        colId: this.$route.query.colId,
        navData: config.navLeft,
        targetName: 4,
        currentPage: 1,
        pageSize: 10,
        nodeList: [],
        total: 0,
        nodeState: config['gwState'],
        ruleForm: {
          nodeNme: '',
          nodeUrl: '',
          nodeDesc: '',
          colId: this.colId
        },
        title: '添加节点',
        rules: {
          nodeName: [{
            required: true,
            message: '请输入节点名称',
            trigger: 'blur'
          }, {
            message: '节点名称只能包括数字、字母、-、_、*',
            trigger: 'change, blur',
            pattern: config.validate.userName
          }],
          nodeDesc: [{
            required: true,
            message: '请输入节点描述',
            trigger: 'blur'
          }],
          nodeUrl: [{
            required: true,
            message: '请输入节点ip',
            trigger: 'change, blur',
            pattern: config.validate.ip
          }]
        },
        visible: false,
        index: '',
        searchRuleForm: {
          nodeName: '',
          nodeStatus: ''
        }, // 搜索
        searchRuleData: {
          nodeName: {
            name: '节点名称:',
            type: 'input',
            placeholder: '请输入节点名称'
          },
          nodeStatus: {
            name: '状态:',
            type: 'select',
            data: {'': '全部', ...config['gwState']},
            placeholder: '请选择状态'
          }
        } // 搜索列表
      }
    },
    created () {
      this.fetch(this.colId, this.currentPage, this.pageSize)
    },
    methods: {
      formatter (row) {
        // return this.nodeState[row.nodeStatus]
        return this.nodeState['1']
      },
      //获取列表
      fetch (colId, currentPage, pageSize) {
        userService.getResponse('nodeList', {colId, currentPage, pageSize}).then((response) => {
            this.nodeList = response.items
            this.total = response.totalNumber
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch(this.colId, this.currentPage, this.pageSize)
      },
      // 删除
      delNode (index, id) {
        this.$confirm('此操作将永久删除该节点, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            userService.getResponse('delNode', {nodeId: id})
              .then((response) => {
                this.nodeList.splice(index, 1)
                this.total--
            })
            .catch(error => {
              this.$message.error(error)
            })
        }).catch()
      },
      //打开弹窗
      showPop (nIndex) {
        this.visible = true
        this.index = nIndex
        let targetData = this.nodeList[nIndex]
        if (targetData) {
          this.title = '修改节点'
          this.ruleForm = {...targetData}
        } else {
          this.title = '添加节点'
          this.ruleForm = {
            nodeName: '',
            nodeUrl: '',
            nodeDesc: '',
            colId: this.colId
          }
        }
      },
      submitForm (ruleForm) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            if (this.ruleForm.nodeId) {
              this.modNode()
            }else{
              this.addNode()
            }
          } else {
            this.$message.error('保存失败!!')
            return false
          }
        })
      },
      //添加功能分类
      addNode () {
        userService.getResponse('addNode', this.ruleForm)
          .then((response) => {
            // this.ruleForm.nodeId = response.nodeId
            // this.nodeList.length < this.pageSize-1 ? this.nodeList.unshift(this.ruleForm) : this.nodeList.unshift(this.ruleForm).pop()
            this.visible = false
            // this.total++
            this.fetch(this.colId,1,this.pageSize)
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      //修改功能分类
      modNode () {
        userService.getResponse('modNode', this.ruleForm)
          .then((response) => {
            this.nodeList[this.index].nodeName = this.ruleForm.nodeName
            this.visible = false
          })
          .catch(error => {
            this.$message.error(error)
          })
      }
    },
    components: { compontLeft, compontSearch }
  }
</script>



