const formatDate = (date) => {
  if (!date) {
    return ''
  } else {
    let dateObj = new Date(date)
    let y = dateObj.getFullYear()
    let m = dateObj.getMonth() + 1
    m = m < 10 ? '0' + m : m
    let d = dateObj.getDate()
    d = d < 10 ? ('0' + d) : d
    return y + '-' + m + '-' + d
  }
}

const routeHop = (router, pathName) => {
  router.go(0)
}
export {formatDate, routeHop}



