<template>
    <el-collapse v-model="activeName" class="open-wrap-content">
      <h1 class="open-api-title">{{ apiChName }}</h1>
      <p class="open-api-detail">{{ desc }}</p>
      <el-collapse-item :title="item.title" :name="index" v-for="(item, index) in ruleForm">
        <p v-if="item.type">{{item.data}}</p>
        <el-table :data="item.data" border class="open-api-detail" v-else>
          <el-table-column :prop="key" :label="val" v-for="(val, key) in item.head"></el-table-column>
        </el-table>
      </el-collapse-item>
      <compontCheck @batchReview="submitCheck" v-if="$route.query.check"></compontCheck>
    </el-collapse>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import config from '@/assets/js/config.js'
  import constans from '@/assets/js/constans.js'
  import userService from '@/api/userService.js'
  import compontCheck from '@/components/CHECK.vue'
  export default{
    name: 'api-detail',
    data () {
      return {
        targetName: 0,
        apiId: this.$route.query.apiId,
        states: config.openState,
        apiChName: '',
        desc: '',
        openApplyId: '',
        ruleForm: [
        ],
        activeName: ['1'],
        // groups: [],
        argTypes: config.argTypes,
        requireds: config.requireds,
        httpmds: config.httpmds,
        openState: config.openState,
        // userInfo: this.$store.state.login.userInfo,
        gwRate: config.gwRate // 网关限频
      }
    },
    computed: {
      userInfo () {
        return this.$store.state.login.userInfo
      }
    },
    created () {
      // 获取api详情
      userService.getResponse('showApidetail', {apiId: this.apiId})
        .then((response) => {
          this.formatData(response)
      })
      .catch((error) => {
        this.$message.error(error)
        // this.$router.go(-1)
      })
      // 获取功能组
      /* this.$ajax(config.url.groupList, {categoryId: this.categoryId}, config.method.groupList)
        .then((response) => {
        let oData = response.data
        let message = oData.message
        let nCode = response.code
        if (nCode === '1') {
        let dataList = oData.data
        let allData = dataList.items
        allData.forEach((item) => {
          this.groups.push({
          id: item.id,
          name: item.appName
        })
      })
      } else {
          this.$message.error(message)
      }
    })
    .catch(error => {
      this.$message.error(error)
      }) */
    },
    methods: {
      formatData (data) {
        this.apiChName = data.apiChName
        this.desc = data.desc
        this.openApplyId = data.openApplyId
        let gatewayUrls = data.gatewayUrls[0]
        let wGatewayUrls = data.wGatewayUrls[0]
        let apiType = data.apiType
        // let applyName = data.openApplyEntity.applyName
        let httpMethod = data.httpMethod
        let testEndPoint = data.testEndPoint
        let endPoint = data.endPoint
        let headers = data.headers
        let args = data.args
        let result = data.result
        let codes = data.errCodes
        let exValue = data.retExample.exValue
        let per = data.per
        let rate = data.rate
        let quotaMax = data.quotaMax
        let quotaRenewalRate = data.quotaRenewalRate
        let version = data.apiVersion.version
        let versionRemark = data.apiVersion.versionRemark
        let listenPath = data.listenPath
        let apiDetail = [
          {
            title: '公共参数',
            head:{
              name: '参数名称',
              value: '参数值'
            },
            data: [{
              'name': '内网请求地址',
              'value': gatewayUrls
            },{
              'name': '外网请求地址',
              'value': wGatewayUrls
            }, {
              'name': 'Http方法',
              'value': httpMethod
            }, {
              'name': 'API类型',
              'value': this.states[apiType]
            }, {
              'name': '访问url（相对路径）',
              'value': listenPath
            }, /* {
              'name': '开放应用',
              'value': applyName
            }, {
              'name': '测试URL',
              'value': testEndPoint
            } , {
             'name': '正式URL',
             'value': endPoint
             } , */ {
              'name': '频次',
              'value': rate + '次/秒'
            }/*, {
              'name': '频次周期',
              'value': rate + 's'
            }*/, {
              'name': '最大配额',
              'value': quotaMax + '次'
            }, {
              'name': '配额重置周期',
              'value': this.gwRate.quotaRenewalRate[quotaRenewalRate]
            }, {
              'name': '版本号',
              'value': version
            }, {
              'name': '版本描述',
              'value': versionRemark
            }]
          }, {
            title: 'Header参数',
            head:{
              argName: '参数名称',
              argType: '参数选项',
              required: '是否必须',
              desc: '示例值',
              example: '描述'
            },
            data: this.convertData(headers)
          }, {
            title: '应用参数',
            head:{
              argName: '参数名称',
              argType: '参数选项',
              required: '是否必须',
              desc: '示例值',
              example: '描述'
            },
            data: this.convertData(args)
          }, {
            title: '返回类型',
            head:{
              retName: '参数名称',
              retType: '参数选项',
              desc: '示例值',
              example: '描述'
            },
            data: result
          }, {
            title: '返回示例',
            type: 'textarea',
            data: exValue
          }, {
            title: '错误代码',
            head:{
              errname: '错误码',
              desc: '描述'
            },
            data: codes
          },/* {
            title: '频次限制',
            head:{
              name: '名称',
              value: '值'
            },
            data: [{
              'name': '频次',
              'value': per + '次'
            }, {
              'name': '频次周期',
              'value': rate + 's'
            }, {
              'name': '最大配额',
              'value': quotaMax
            }, {
              'name': '配额重置周期',
              'value': quotaRenewalRate
            }]
          } */
        ]
        !(this.$route.meta && this.$route.meta.opera) && apiDetail[0].data.splice(3, 1)
        /*apiDetail.push({
          title: '版本信息',
          head:{
            name: '参数名称',
            value: '参数值'
          },
          data: [{
            'name': '版本号',
            'value': version
          }, {
            'name': '版本描述',
            'value': versionRemark
          }]
        })*/
        this.ruleForm.push(...apiDetail)
      },
      convertData (data) {
        data.forEach(function (item) {
          let required = item.required
          item.required = config['requireds'][required]
        })
        return data
      },
      // 审核
      submitCheck(ruleForm) {
        ruleForm.apiIds = this.apiId
        this.visible = false
        userService.getResponse('batchApi', ruleForm)
          .then(response => {
            this.$router.go(-1)
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    components: { compontCheck }
  }
</script>



