<template>
  <aside class="open-crumbs">
    <p class="open-crumbs-wrap">
      <!--<router-link class="open-crumbs-detail" to="/index">首页</router-link>-->
      <router-link :to="item.path" class="open-crumbs-detail" v-for="item in curmbsData">{{item.title}}</router-link>
    </p>
  </aside>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import * as types from '@/store/mutation-types'
  import { eventHub } from '@/eventHub'
  export default{
    name: 'left',
    data () {
      return {
        curmbsData: [],
        title: ''
      }
    },
    created() {
      this.getCurmbs()
      eventHub.$on('changeTitle', (title) => { //Hub接收事件
        this.title = title
        this.getCurmbs()
        this.title = ''
      });
    },
    watch: {
      '$route': 'getCurmbs'
    },
    methods: {
      getCurmbs() {
        let name = this.$route.query.name || ''
        let routerMeta = this.$route.meta
        let curmbsIndex = routerMeta.curmbsIndex
        let curmbsLength = this.$store.state.curmbs.curmbsData.length
        let routerTitle = name + (this.title || routerMeta.title)
        let fullPath = this.$route.fullPath
        if (curmbsIndex === 1 || curmbsIndex === curmbsLength) {// 当前点击面包屑层级是1的时候删掉curmbsData所有并添加当前点击的数据
          this.removeCurmbsData(curmbsIndex)
          this.addCurmbsData(routerTitle, fullPath)
        } else {
          if (curmbsIndex > curmbsLength) {// 当前页面层级大于面包屑数组  即添加数据
            this.addCurmbsData(routerTitle, fullPath)
          } else {
            this.removeCurmbsData(curmbsIndex+1) // 从面包屑里面点击
          }
        }
        this.curmbsData = this.$store.state.curmbs.curmbsData
      },
      // 添加面包屑片段
      addCurmbsData (routerTitle, fullPath) {
        let curmbsPart = {
          title: routerTitle,
          path: fullPath
        }
        this.$store.commit(types.ADDCURMBSDATA, curmbsPart)
      },
      removeCurmbsData (curmbsIndex) {
        this.$store.commit(types.REMOVECURMBSDATA, curmbsIndex)
      }
    },
    components: {}
  }
</script>



