<template>
    <el-collapse class="open-wrap-content">
      <h1 class="open-api-title">{{ ruleForm.guideName }}</h1>
      <p class="open-api-detail">{{ ruleForm.guideDesc }}</p>
      <compontCheck @batchReview="submitCheck" v-if="check"></compontCheck>
    </el-collapse>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import config from '@/assets/js/config.js'
  import userService from '@/api/userService'
  import compontCheck from '@/components/CHECK.vue'
  export default{
    name: 'guide-detail',
    data () {
      return {
        guideId: this.$route.query.guideId,
        check: this.$route.query.check,
        ruleForm: {}
      }
    },
    created () {
      // 获取接入指南详情
      userService.getResponse('getGuideDetail', {id: this.guideId})
        .then((response) => {
          this.ruleForm = response
        })
        .catch( (error) => {
          this.$message.error(error)
          this.$router.go(-1)
        })
    },
    methods: {
      // 审核
      submitCheck(ruleForm) {
        ruleForm.ids = this.ruleForm.id
        this.visible = false
        userService.getResponse('batchGuide', ruleForm)
          .then(response => {
            this.$router.go(-1)
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    components: { compontCheck }
  }
</script>



