<template>
      <section class="open-wrap-content">
        <el-table :data="gwList" border>
          <!--<el-table-column label="编号" type="index" width="80"></el-table-column>-->
          <el-table-column label="名称">
            <template scope="scope">
              <router-link class="open-list-btn" :to="{name: 'nodeList', query: {colId: scope.row.colId, name: scope.row.colName}}">{{scope.row.colName}}</router-link>
            </template>
          </el-table-column>
          <el-table-column label="代理域名" prop="colUrl"></el-table-column>
          <el-table-column label="状态" prop="colStatus" :formatter="formatter"></el-table-column>
          <el-table-column label="描述" prop="colDesc" show-overflow-tooltip></el-table-column>
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="total, prev, pager, next" :total="total" :page-size="pageSize" class="open-page" v-if="total"></el-pagination>
      </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import userService from '@/api/userService.js'
  export default{
    name: 'gw-list',
    data () {
      return {
        navData: config.navLeft,
        targetName: 4,
        nodeState: config['gwState'],
        currentPage: 1,
        pageSize: 10,
        gwList: [],
        total: 0,
        gwState: config.gwState
      }
    },
    created () {
      this.fetch()
    },
    methods: {
      formatter (row) {
        return this.nodeState[row.colStatus]
      },
      //获取列表
      fetch () {
        let currentPage = this.currentPage
        let pageSize = this.pageSize
        userService.getResponse('gwList', {currentPage, pageSize}).then((response) => {
            this.gwList = response.items
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch()
      },
    },
    components: { compontLeft }
  }
</script>



