<template>
  <el-form :model="ruleForm" class="demo-form-inline" >
    <template v-for="(item, key) in ruleForm">
      <el-form-item :label="ruleData[key]['name']" :prop="key" v-if="ruleData[key] && ruleData[key]['type'] == 'input'">
        <el-input v-model="ruleForm[key]" :placeholder="ruleData[key]['placeholder']"></el-input>
      </el-form-item>
      <el-form-item :label="ruleData[key]['name']" :prop="key" v-if="ruleData[key] && ruleData[key]['type'] == 'select'">
        <el-select v-model="ruleForm[key]" v-if="ruleData[key].isArray">
          <template v-for="optionData in ruleData[key]['data']">
            <el-option v-for="(optionName, index) in optionData" :key="index" :label="optionName" :value="index"></el-option>
          </template>
        </el-select>

        <el-select v-model="ruleForm[key]" v-else>
          <el-option v-for="(optionName, index) in ruleData[key]['data']" :key="index" :label="optionName" :value="index"></el-option>
        </el-select>
      </el-form-item>
    </template>
    <el-form-item>
      <el-button type="primary" @click="onSubmit(ruleForm)">搜索</el-button>
    </el-form-item>
  </el-form>
</template>
<style lang="less" scoped>
</style>
