               2: '审核通过',
               3: '审核未通过'
             }]
           }
         } */
      }
    },
    props: ['ruleForm', 'ruleData'],
    created () {
      console.log(this.ruleData)
    },
    methods: {
      onSubmit (ruleForm) {
        this.$emit('searchData', this.ruleForm)
      }
        /*this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            this.$emit('searchData', this.ruleForm)
            //this.searchData()
          } else {
            this.$message.error('保存失败!!')
            return false
          }
        })
      }*/
    },
    components: {}
  }
</script>



