<template>
  <section>
    <el-table :data="tableData.data" border @selection-change="handleSelectionChange">
      <template v-for="(item, index) in tableData.head">
        <el-table-column type="selection" width="55" v-if="item.select" :selectable="(row)=>{return item.state && item.state.includes(row.checkState)}"></el-table-column>
        <template v-else-if="item.prop">
          <el-table-column :label="item.label" :prop="item.prop" show-overflow-tooltip v-if="item.prop == 'checkState'"><!--状态-->
            <template scope="scope">
              <el-popover trigger="hover" placement="top" v-if="scope.row.checkState == constans['STATE_NOPASS']">
                <p v-if="scope.row.checkMem">{{ scope.row.checkMem }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag>{{ checkState[scope.row.checkState]}}</el-tag>
                </div>
              </el-popover>
              <p v-else>
                {{checkState[scope.row.checkState]}}
              </p>
            </template>
          </el-table-column>

          <el-table-column :label="item.label" :prop="item.prop" show-overflow-tooltip v-else-if="item.prop =='createTime'" :formatter="formatter">
          </el-table-column>

          <el-table-column :label="item.label" :prop="item.prop" show-overflow-tooltip v-else-if="item.route">
            <template scope="scope">
              <router-link class="open-list-btn" :to="dealUrl(scope, item.route)">{{ scope.row[item.prop] }}</router-link>
            </template>
          </el-table-column><!--名称可点击-->
          <el-table-column :label="item.label" :prop="item.prop" v-else-if="item.initData">
            <template scope="scope">
              {{item.initData[scope.row[item.prop]]}}
            </template>
          </el-table-column><!--需要处理-->
          <el-table-column :label="item.label" :prop="item.prop" show-overflow-tooltip v-else></el-table-column><!--普通-->
        </template>
        <el-table-column :label="item.label" v-else><!--操作-->
          <template scope="scope">
            <template v-if="!item.data.length">
              ——
            </template>
            <template v-else>
              <template v-if="(value.state && value.state.includes(scope.row.checkState)) || !value.state" v-for="value in item.data">
                <a class="open-list-btn" @click="value.callBack(scope.row)" v-if="value.callBack">{{ value.name }}</a>
                <router-link class="open-list-btn" :to="dealUrl(scope, value.route)" v-if="value.route">{{ value.name }}</router-link>
              </template>
            </template>
          </template>
        </el-table-column>
      </template>
    </el-table>
    <el-button @click="value.callBack(multipleSelection)" v-for="value in tableData.btn" :disabled=!Boolean(multipleSelection.length)>{{ value.name }}</el-button>
    <el-pagination @current-change="handleCurrentChange" layout="total, prev, pager, next" :total="tableData.pageData.total*1" :page-size="tableData.pageData.pageSize" :current-page="tableData.pageData.currentPage" class="open-page" v-if="tableData.pageData.total"></el-pagination>
  </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import {formatDate} from '@/assets/js/lib.js'
  export default{
    name: 'table',
    data () {
      return {
        multipleSelection: [],
        constans: constans, // 常量
        checkState: config.checkState
      }
    },
    /*
    * tableData: {
    *   head: [
    *     {label: 'API列表', prop:'apiChName'},
     *    {label: '操作', data: [
     *      {'name': '查看', 'callBack': funDetail}
     *    ]}
    *   ],
    *   data: [
    *   {
    *     'id': '1111111',
    *     'apiChName': '11111',
    *     'funDetail': funDetail()
    *   },
    *   ],
    *   pageData: {
    *     total: 100,
    *     pageSize： 10
    *   }
    * }
    * */
    props: ['tableData'],
    methods: {
       handleCurrentChange (pIndex) {
        this.$emit('handleCurrentChange', pIndex)
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      dealUrl(scope, route) {
        let param = {name: route.name, query:{}}
        for (var key in route.query) {
          param.query[key] = scope.row[route.query[key]]
        }
        for (var textKey in route.textQuery){
          param.query[textKey] = route.textQuery[textKey]
        }
        return param
      },
      formatter (row, column) {
        return row.createTime && formatDate(row.createTime)
      },
    },
    components: {}
  }
</script>



