<template>
  <div id="app">
    <router-view name="header"></router-view>
    <router-view name="compontCurmbs" :curmbsData="curmbsData"></router-view>
    <router-view></router-view>
    <router-view name="footer"></router-view>
  </div>
</template>

<script>
  import * as types from '@/store/mutation-types.js'
  // import config from '@/assets/js/config.js'
  export default {
    name: 'app',
    data () {
      return {
        curmbsData: this.$route.meta.curmbsData
        // userInfo: []
      }
    },
    watch: {
      '$route': 'fetchData'
    },
    created () {
      // 刷新
      // this.checkLogin()
    },
    methods: {
      fetchData () {
        // 路由改变
        this.curmbsData = this.$route.meta.curmbsData
        // this.checkLogin()
      },
      checkLogin () {
        if (!this.$store.state.login.userInfo) {
          let userInfo = sessionStorage.getItem('session')
          userInfo && this.$store.commit(types.UPDATEUSERINFO, JSON.parse(userInfo))
          /* this.$ajax(config.url.checkLogin, {}, config.method.checkLogin).then(response => {
            let oData = response.data
            let nCode = oData.code
            let userInfo = oData.data
            if (nCode === '1') {
              this.$store.commit(types.UPDATEUSERINFO, userInfo)
              this.userInfo = this.$store.state.login.userInfo
            } else {
              this.$router.push({
                name: 'login'
              })
            }
          }) */
        }
        // this.userInfo = this.$store.state.login.userInfo
      }
    }
  }
</script>

<style lang = "less">
  #app{
    width: 100%;
    height: 100%;
  }
</style>



