<template>
  <nav class="open-control-nav" v-if="navData">
    <p class="nav-left-title">{{ navData.title }}</p>
    <template v-for="(nav, index) in navData.data">
      <router-link class="nav-left-list nav-left-active" data-id="nav.id" :to="{ name: nav.routerName }" @click.native="routeHop(nav.routerName)" v-if="index == targetName">{{ nav.name }}</router-link>
      <router-link class="nav-left-list" :to="{ name: nav.routerName }" @click.native="routeHop(nav.routerName)" v-else>{{ nav.name }}</router-link>
    </template>
  </nav>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import { routeHop } from '@/assets/js/lib'
  export default{
    name: 'left',
    data () {
      return {}
    },
    computed: {
      navData () {
        return this.getNavLeft(this.$route.matched)
      },
      name () {
        return this.$route.name
      },
      targetName () {
        let matched = this.$route.matched
        let len = matched.length-1
        let route = matched[len]
        let num
        while(route){

          num = this.navData.data.findIndex((option) => {
            return option.routerName === route.name || (option.routerName === (route.redirect && route.redirect.name))
          })

          if(num < 0){
            route = matched[--len]
          }else{
            return num
          }
        }

        return num
      }

    },
    created () {},
    methods: {
      getNavLeft (matched) {
        let len = matched.length;
        let route = len && matched[len-1];
        while(route){
          if(route.meta && route.meta.navLeft){
            return route.meta.navLeft
          }
          route = route.parent;
        }
      },
      routeHop (pathName) {
        routeHop(this.$router, pathName)
      }
    }
  }
</script>



