<template>
    <section>
      <compontSearch @searchData="fetch(1)" :ruleForm="searchRuleForm" :ruleData="searchRuleData"></compontSearch>
      <compontTable :tableData ="applyList" @handleCurrentChange="handleCurrentChange"></compontTable>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import userService from '@/api/userService'
  import compontTable from '@/components/TABLE.vue'
  import compontSearch from '@/components/SEARCH.vue'
  export default{
    name: 'open-apply',
    data () {
      return {
        constans: constans, // 常量
        productId: this.$route.query.productId,
        // diyApplyId: this.$route.query.applyId,
        applyList: {
          head: [{
            label: '应用名称', prop: 'appName', route: {name: this.$route.meta.pName + 'AppDetail', query: {appId: 'appId', canOpera: 'appId', name: 'appName'}, textQuery: {diyApplyId: this.$route.query.applyId}}
          }, {
            label: '应用key', prop: 'appCode'
          }, {
            label: '描述', prop: 'appDesc'
          }, {
            label: '操作', data: [{
              name: 'API列表',
              route: { name: this.$route.meta.pName+'AppDetail', query: { appId: 'appId', canOpera: 'appId', appCode: 'appCode', name: 'appName'}}
            }, {
              name: '使用指南',
              route: { name: this.$route.meta.pName+'AppDetail', query: { appId: 'appId', canOpera: 'appId', appCode: 'appCode', name: 'appName'}, textQuery: {active: 'guideList'}}
            }]
          }],
          data: [],
          pageData: {
            total: 0,
            pageSize: 10,
            currentPage: 1
          }
        },
        searchRuleForm: {
          name: ''
        }, // 搜索
        searchRuleData: {
          name: {
            name: '应用名称:',
            type: 'input',
            placeholder: '请输入开放应用名称'
          }
        } // 搜索列表
      }
    },
    props: ['type'],
    created () {
      this.fetch()
    },
    methods: {
      //获取列表
      fetch (currentPage = this.applyList.pageData.currentPage) {
        let pageSize = this.applyList.pageData.pageSize
        this.applyList.data = []
        let name = this.searchRuleForm.name
        this.applyList.pageData.total = 0
        if (this.productId) {
          this.getAllApplyByPro(currentPage, pageSize, name)
        } else {
          this.getAllApply(currentPage, pageSize, name)
        }
      },
      getAllApply (currentPage, pageSize, name) {
        userService.getResponse(this.type, {currentPage, pageSize, name})
          .then((response) => {
            let applyList = response.data.list
            this.applyList.data = applyList
            this.applyList.pageData.total = response.data.totalCount
            this.applyList.pageData.currentPage = currentPage
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      getAllApplyByPro (currentPage, pageSize, name) {
        userService.getResponse('findTenantAppsByTenantKey', {currentPage:currentPage, pageSize:pageSize, key: this.productId, appName: name})
          .then((response) => {
            let applyList = response.data.pages.list
            this.applyList.data = applyList
            this.applyList.pageData.total = response.data.pages.totalCount
            this.applyList.pageData.currentPage = currentPage
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.applyList.pageData.currentPage = pIndex
        this.fetch()
      }
    },
    components: { compontTable, compontSearch }
  }
</script>



