<template>
  <section class="open-wrap-content">
    <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick">
      <!--<el-tab-pane label="开放应用信息">
        <p class="open-person-info" v-for="appInfo in appDetail"><span class="open-person-name">{{ appInfo.name }}</span>{{ appInfo.value }}</p>
        <compontCheck @batchReview="submitCheckApp" v-if="$route.query.check"></compontCheck>
      </el-tab-pane>-->

      <template  v-if="$route.query.canOpera">
        <el-tab-pane label="API列表" name="apiList">
          <compontSearch @searchData="getApiList(1)" :ruleForm="searchRuleForm" :ruleData="searchRuleData"></compontSearch>
         <!-- <router-link :to="{name: 'addApi', query: {applyId: appId}}" class="open-group-add" v-if="$route.meta.opera">添加API</router-link>-->
          <compontTable :tableData ="apiList" @handleCurrentChange="handleCurrentChangeApi"></compontTable>
        </el-tab-pane>
        <el-tab-pane label="接入指南列表" name="guideList">
          <compontSearch @searchData="getGuideList(1)" :ruleForm="searchGuideForm" :ruleData="searchGuideData"></compontSearch>
          <!--<router-link :to="{name: 'addGuide', query: {applyId: appId}}" class="open-group-add" v-if="$route.meta.opera">添加接入指南</router-link>-->
          <compontTable :tableData ="guideList" @handleCurrentChange="handleCurrentChangeGuide"></compontTable>
        </el-tab-pane>
      </template>
    </el-tabs>
    <el-dialog title="批量审核" :visible.sync="visible">
      <compontCheck @batchReview="submitCheck"></compontCheck>
    </el-dialog>
  </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import userService from '@/api/userService.js'
  import compontTable from '@/components/TABLE.vue'
  import compontCheck from '@/components/CHECK.vue'
  import compontSearch from '@/components/SEARCH.vue'
  import { eventHub } from '@/eventHub'
  export default{
    name: 'api-list-common',
    data () {
      return {
        appId: this.$route.query.appId,
        checkState: '', // 开放应用的状态
        activeName: this.$route.query.active || 'apiList',
        diyApplyId: this.$route.query.diyApplyId,
        appDetail: [],
        groupList: [],
        apiType: config.openState,
        // apiListUrl: 'docApiList', // api列表接口
        apiList: {
          head: [{
            label: 'API名称', prop: 'apiChName', route: {name: this.$route.meta.pName + 'ApiDetail', query: { apiId: 'id', name: 'apiChName'}}
          }, {
            label: '版本号', prop: 'apiVersion.version'
          }, {
            label: '描述', prop: 'apiDesc'
          }, {
            label: '操作', data: []
          }],
          data: [],
          pageData: {
            total: 0,
            pageSize: 10,
            currentPage: 1
          }
        },
        guideList: {
          head: [{
            label: '指南名称', prop: 'guideName', route: { name: this.$route.meta.pName + 'GuideDetail', query: { guideId: 'id', name: 'guideName'}}
          }, {
            label: '描述', prop: 'guideDesc'
          }, {
            label: '操作', data: []
          }],
          data: [],
          pageData: {
            total: 0,
            pageSize: 10,
            currentPage: 1
          }
        }, // 接入指南
        constans: constans,
        visible: false,
        searchRuleForm: {
          openApplyId: this.$route.query.appId,
          apiChName: '',
          checkState: '',
          apiType: ''
        }, // 搜索
        searchRuleData: {
          apiChName: {
            name: 'API名称:',
            type: 'input',
            placeholder: '请输入API名称'
          },
          checkState: {
            name: '状态:',
            type: 'select',
            data: {'': '全部', ...config['checkState']},
            placeholder: '请选择状态'
          }
        }, // 搜索列表
        searchGuideForm: {
          applyId: this.$route.query.appId,
          guideName: '',
          checkState: ''
        }, // 搜索
        searchGuideData: {
          guideName: {
            name: 'API名称:',
            type: 'input',
            placeholder: '请输入API名称'
          },
          checkState: {
            name: '状态:',
            type: 'select',
            data: {'': '全部', ...config['checkState']},
            placeholder: '请选择状态'
          }
        } // 搜索列表
      }
    },
    computed: {
    },
    created () {
      let tab = {}
      tab.label = this.activeName==='apiList' ? 'API列表': '接入指南列表'
      this.handleClick(tab)
      if (this.$route.query.canOpera && this.$route.meta.opera) { // 可以操作 // 提供者控制台进来的api列表  展示开放、保留的api。其他途径进来的api只展示开放
        this.apiList.head.unshift({
          select: true,
          state:[constans['STATE_UNAUDITED']]
        })
        this.apiList.head.splice(3, 0, {
          label: '状态', prop: 'checkState',
        }, {
          label: 'API类型', prop: 'apiType', initData: this.apiType
        })
        this.apiList.head[this.apiList.head.length-1].data.unshift({
          name: '审核',
          route: { name: this.$route.meta.pName+'ApiDetail', query: { apiId: 'id', check: 'checkState'}},
          state:[constans['STATE_UNAUDITED']]
        })
        this.apiList.btn = [{ // 批量操作
          'name': '批量审核', 'callBack': this.showPop
        }]

        this.guideList.head.unshift({
          select: true,
          state:[constans['STATE_UNAUDITED']]
        })
        this.guideList.head.splice(2, 0, {
          label: '状态', prop: 'checkState',
        })
        this.guideList.head[this.guideList.head.length-1].data.unshift({
          name: '审核',
          route: { name: this.$route.meta.pName + 'GuideDetail', query: { guideId: 'id', check: 'checkState'}},
          state:[constans['STATE_UNAUDITED']]
        })
        this.guideList.btn = [{ // 批量操作
          'name': '批量审核', 'callBack': this.showPopGuide
        }]

        this.searchRuleData.apiType = {
          name: 'API类型:',
          type: 'select',
          data: {'': '全部', ...config['openState']},
          placeholder: '请选择API类型'
        }

        // 添加API类型
        // this.apiListUrl = 'apiList'
      }

      this.getApiList()
      this.getGuideList()
      // this.getApplyDetail()
    },
    methods: {
      //获取api列表
      getApiList (currentPage = this.apiList.pageData.currentPage) {
        this.apiList.pageData.total = 0
        this.apiList.data = []
        this.diyApplyId ? this.getLimitList(currentPage) : this.getAllApiList(currentPage)
      },
      //获取所有api列表
      getAllApiList (currentPage) {
        let pageSize = this.apiList.pageData.pageSize
        userService.getResponseByBody('apiList', {currentPage, pageSize}, this.searchRuleForm)
          .then((response) => {
            let apiList = response.items
            this.apiList.data = apiList
            this.apiList.pageData.total = response.totalNumber
            this.apiList.pageData.currentPage = currentPage
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      //获取api列表
      getLimitList (currentPage) {
        let pageSize = this.apiList.pageData.pageSize
        let openApplyId = this.appId
        let diyApplyId = this.diyApplyId
        let apiName = this.searchRuleForm.apiChName
        userService.getResponse('getLimitScope', {currentPage, pageSize, apiName, openApplyId, diyApplyId})
          .then((response) => {
            let apiList = response.items
            this.apiList.data = apiList
            this.apiList.pageData.total = response.totalNumber
            this.apiList.pageData.currentPage = currentPage
          })
          .catch(error => {
            // this.$message.error(error)
          })
      },
      //获取接口指南详情
      getGuideList (currentPage = this.guideList.pageData.currentPage) {
        this.guideList.pageData.total = 0
        this.guideList.data = []
        let pageSize = this.guideList.pageData.pageSize
        userService.getResponseByBody('guideList', {currentPage, pageSize}, this.searchGuideForm)
          .then((response) => {
            this.guideList.data = response.items
            this.guideList.pageData.total = response.totalNumber
            this.guideList.pageData.currentPage = currentPage
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      // 分页api列表
      handleCurrentChangeApi (pIndex=1) {
        this.apiList.pageData.currentPage = pIndex
        this.getApiList()
      },
      // 分页指南列表
      handleCurrentChangeGuide (pIndex=1) {
        this.guideList.pageData.currentPage = pIndex
        this.getGuideList(this.currentPage)
      },
      // 审核开放应用
      submitCheckApp (ruleForm) {
        ruleForm.ids = this.appId
        this.visible = false
        userService.getResponse('batchOpenApply', ruleForm)
          .then(response => {
            this.$router.push({
              name: 'openApplyMan'
            })
          })
          .catch(error => {
            console.log(error)
          })
      },
      // 点击批量审核
      submitCheck(ruleForm) {
        this.visible = false
        switch (this.type){
          case 'api':
            this.submitCheckApi(ruleForm)
            break;
          case 'guide':
            this.submitCheckGuide(ruleForm)
            break;
        }
      },
      // 审核api
      submitCheckApi(ruleForm) {
        ruleForm.apiIds = this.id
        userService.getResponse('batchApi', ruleForm)
          .then(response => {
            this.$message.success('审核成功')
            this.getApiList()
          })
          .catch(error => {
            console.log(error)
          })
      },
      // 审核指南
      submitCheckGuide(ruleForm) {
        ruleForm.ids = this.id
        userService.getResponse('batchGuide', ruleForm)
          .then(response => {
            this.$message.success('审核成功')
            this.getGuideList()
          })
          .catch(error => {
            console.log(error)
          })
      },
      //打开弹窗
      showPop (data) {
        this.visible = true
        this.type='api'
        let id = []
        if (data.constructor === Array) {
          data.map(item=>{
            id.push(item.id)
          })
          this.id = id.join(',')
        } else {
          this.id = data.id
        }
      },
      showPopGuide (data) {
        this.visible = true
        this.type='guide'
        let id = []
        if (data.constructor === Array) {
          data.map(item=>{
            id.push(item.id)
          })
          this.id = id.join(',')
        } else {
          this.id = data.id
        }
      },
      handleClick (tab) {
        let title = tab.label
        eventHub.$emit('changeTitle', title); //Hub触发事件
      }
    },
    components: { compontTable, compontCheck, compontSearch }
  }
</script>



