import constans from './constans.js'
const config = {
  tyk: 'http://www.tyk/',
  validate: {
    userName: /[\w-*]+$/,
    passWord: /^[a-zA-Z0-9]+[\w.\-*@?!]+$/,
    email: /^[\w-]+@[\w-]+(\.[\w-]+)+$/,
    tel: /^((13[0-9])|(15[^4])|(18[0-9])|(17[0-8])|(147,145))\d{8}$/,
    apiChName: /[\u4e00-\u9fa5]+$/,
    apiEnName: /[a-zA-Z0-9]+$/,
    ip: /((2[0-4]\d|25[0-5]|[01]?\d\d?)\.){3}(2[0-4]\d|25[0-5]|[01]?\d\d?)/,
    paramName: /^[a-zA-Z]+\w\$$/,
    errname: /^\\w+[\w.-]+$/,
    appName: /[\u4e00-\u9fa50-9\w-*]+$/
  },
  expiredTime: 3600 * 24 * 1000, // 过期时间
  userInfo: { // 保存用户信息
    userName: null,
    mem: null,
    id: null
  },
  argTypes: [
    'int', 'string', 'data', 'float', 'double'
  ],
  httpmds: ['GET', 'POST'],
  code: {
    0: '失败',
    1: '成功',
    3: '用户名已存在'
  },
  gwRate: {
    maxRate: 100, // 最大频次
    quotaMax: 5000, // 最大配额
    quotaRenewalRate: {
      '3600000': '1小时', '84600000': '1天'
    }
  },
  pageSize: 10000,
  resetPwd: '123456',
  userType: {
  },
  openState: {
  },
  requireds: {
  },
  navLeft: {
  },
  gwState: {
  },
  checkState: {
  },
  userCheckState: {}
}
config['userType'][constans['ADMIN_USER']] = '管理员'
config['userType'][constans['SERVICE_USER']] = '普通用户'
config['userType'][constans['SUPPLY_USER']] = 'API提供者'

config['openState'][constans['API_OPEN']] = '开放'
config['openState'][constans['API_RETAIN']] = '保留'

config['gwState'][constans['STATE_OPEN']] = '启用'
config['gwState'][constans['STATE_RESERVE']] = '禁用'

config['checkState'][constans['STATE_UNCOMMITTED']] = '未提交'
config['checkState'][constans['STATE_UNAUDITED']] = '未审核'
config['checkState'][constans['STATE_PASS']] = '审核通过'
config['checkState'][constans['STATE_NOPASS']] = '审核驳回'

config['userCheckState'][constans['STATE_UNCOMMITTED']] = '未提交认证'
config['userCheckState'][constans['STATE_UNAUDITED']] = '已提交认证'
config['userCheckState'][constans['STATE_PASS']] = '认证通过'
config['userCheckState'][constans['STATE_NOPASS']] = '认证未通过'

config['requireds'][constans['REQUIRED']] = '是'
config['requireds'][constans['NO_REQUIRED']] = '否'

config['navLeft'] = {
  data: [{
    name: '提供者管理',
    routerName: 'supplyMan'
  }, {
    name: '开发者管理',
    routerName: 'developerMan'
  }, {
    name: '开放应用管理',
    routerName: 'openApplyMan'
  }, {
    name: '定制应用管理',
    routerName: 'diyApplyMan'
  }, {
    name: '网关管理',
    routerName: 'gwList'
  }]
}
export { config as default }



